@import url('https://fonts.googleapis.com/css2?family=Maven+Pro:wght@600&family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap');

:root {
    --white: #ffffff;
    --black: #3a3a3a;
    --grey-dark: #8c8c8c;
    --grey-light: rgb(230, 230, 230);
    --primary: #228be6;
    --primary-hover: #1273c9;
    --background-light: #e1eaff;
}

* {
    font-family: 'Montserrat', sans-serif;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
