.tw-dash-page .tippy-box[data-placement^="top"] > .tippy-arrow {
  bottom: 0;
}

.tw-dash-page .tippy-box[data-placement^="top"] > .tippy-arrow:before {
  border-top-color: initial;
  border-width: 8px 8px 0;
  bottom: -7px;
  left: 0;
  transform-origin: center top;
}

.tw-dash-page .tippy-box[data-placement^="bottom"] > .tippy-arrow {
  top: 0;
}

.tw-dash-page .tippy-box[data-placement^="bottom"] > .tippy-arrow:before {
  border-bottom-color: initial;
  border-width: 0 8px 8px;
  left: 0;
  top: -7px;
  transform-origin: center bottom;
}

.tw-dash-page .tippy-box[data-placement^="left"] > .tippy-arrow {
  right: 0;
}

.tw-dash-page .tippy-box[data-placement^="left"] > .tippy-arrow:before {
  border-left-color: initial;
  border-width: 8px 0 8px 8px;
  right: -7px;
  transform-origin: center left;
}

.tw-dash-page .tippy-box[data-placement^="right"] > .tippy-arrow {
  left: 0;
}

.tw-dash-page .tippy-box[data-placement^="right"] > .tippy-arrow:before {
  border-right-color: initial;
  border-width: 8px 8px 8px 0;
  left: -7px;
  transform-origin: center right;
}

.tw-dash-page .tippy-box[data-inertia][data-state="visible"] {
  transition-timing-function: cubic-bezier(0.54, 1.5, 0.38, 1.11);
}

.tw-dash-page .tippy-arrow {
  color: #333;
  height: 16px;
  width: 16px;
}

.tw-dash-page .tippy-arrow:before {
  border-color: transparent;
  border-style: solid;
  content: "";
  position: absolute;
}

.tw-dash-page .tippy-content {
  padding: 5px 9px;
  position: relative;
  z-index: 1;
}

.tw-dash-page .tippy-box[data-placement^="top"] > .tippy-svg-arrow {
  bottom: 0;
}

.tw-dash-page .tippy-box[data-placement^="top"] > .tippy-svg-arrow:after,
.tw-dash-page .tippy-box[data-placement^="top"] > .tippy-svg-arrow > svg {
  top: 16px;
