}

.tw-dash-page .tippy-box[data-placement^="bottom"] > .tippy-svg-arrow {
  top: 0;
}

.tw-dash-page .tippy-box[data-placement^="bottom"] > .tippy-svg-arrow > svg {
  bottom: 16px;
}

.tw-dash-page .tippy-box[data-placement^="left"] > .tippy-svg-arrow {
  right: 0;
}

.tw-dash-page .tippy-box[data-placement^="left"] > .tippy-svg-arrow:after,
.tw-dash-page .tippy-box[data-placement^="left"] > .tippy-svg-arrow > svg {
