.tw-dash-page .tippy-box[data-placement^="right"] > .tippy-svg-arrow {
  left: 0;
}

.tw-dash-page .tippy-box[data-placement^="right"] > .tippy-svg-arrow:after,
.tw-dash-page .tippy-box[data-placement^="right"] > .tippy-svg-arrow > svg {
