}

.tw-dash-page .fa-solid,
.tw-dash-page .fas {
  font-weight: 900;
}

/* USER CSS */

.tw-dash-page .single-chart {
  width: 33%;
  justify-content: space-around;
}

.tw-dash-page .circular-chart {
  display: block;
  margin: 10px auto;
  max-height: 250px;
}

.tw-dash-page .circle-bg {
  fill: none;
  stroke: #eee;
  stroke-width: 3.8;
}

.tw-dash-page .circle {
  fill: none;
  stroke-width: 2.8;
  stroke-linecap: round;
  animation: progress 1s ease-out forwards;
  -webkit-animation: progress 1s ease-out forwards;
}

.tw-dash-page .circular-chart.orange .circle {
  stroke: #ff9f00;
}

.tw-dash-page .circular-chart.green .circle {
  stroke: #4cc790;
}

.tw-dash-page .circular-chart.blue .circle {
  stroke: #3c9ee5;
}

.tw-dash-page .percentage {
  fill: #666;
  font-family: sans-serif;
  font-size: 0.5em;
  text-anchor: middle;
}

.noHoverCard:hover{
    transform:none!important;
    background-color:#fff !important;
}
.itemCard{
  border-radius: 6px;
  justify-content: space-between;
}
.itemCard:hover{
  background-color: #f2f2f2!important;

}
.scrollbar{
  scrollbar-color:var(--tw-grayscale);
    scrollbar-width: thin;
}
/* width */
::-webkit-scrollbar {
  width: 6px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #fff;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #ccc;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}